.accordionContainer {
    border: 1px solid #eee;
    margin: 10px;
}
.accordionHeader {
    border-bottom: 1px solid #eee;
    padding: 10px 10px 10px 30px;
    background: #b9ecf1b3;
    cursor: pointer;
}
p.accordionTitle {
    font-size: 20px;
    text-transform: capitalize;
    font-weight: 500;
}
.accordionBody {
    min-height: 200px;
    background: #c3e4ea4f;
    padding: 20px;
}
.leftIcon {
    display: inline-block;
}
.rightIcon {
    float: right;
}
button.indIcon.btn {
    font-size: 20px;
    font-weight: 700;
    background: none;
}