.header {
    background-color: #c3e3ea;
    min-height: 50px;
}

.imageContainer {
    height:50px;
    width:50px;
    overflow:hidden;
    border-radius: 50%;
    margin:10px 100px;
}
.image {
    height:100%;
    width:100%
}
li.nav-link {
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    text-decoration: none;
}

ul li.nav-link  a.link {
    background-color: #c3e3ea;
    color:#75827f;
}
ul li.nav-link a.link:hover,
    ul li.nav-link a.link:active {
    background-color: #c3e3ea;
    color:#333;
}
@media screen and (max-width:767.5px){
    .imageContainer{
        margin-left: auto;
        margin-right: auto;
    }
}
.navbar-nav {
    margin-left: 0px;
    margin-right: 0px;
}